.my-account-container {
	width: 60%;
	margin: 2.5% auto;
}

.my-personal-info form {
	width: 100%;
	margin: 1rem 0;
}

.edit-info-buttons button {
	margin-right: 1em;
}

.other-page-buttons {
	margin: 1rem auto;
}

.other-page-buttons button{
	margin-right: 1em;
}

input[type=text], input[type=number] {
	width: 500px;
	padding: 12px 20px;
	margin: 8px 0;
	display: inline-block;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-sizing: border-box;
}

input[type=submit], input[type=button] {
	width: 200px;
	background-color: #4CAF50;
	color: white;
	padding: 14px 20px;
	margin: 5%;
	border: none;
	border-radius: 4px;
	cursor: pointer;
}

$react-rater-link: #ccc !default;
$react-rater-active: #000 !default;
